import React, { Component } from 'react';

class Proficiency extends Component {
  render() {
    return (
      <div className="light-blue">
        <div className="col-md-12" id="demos">
          <div className="text-center">
            <h1>Proficiency</h1>
          </div>
          <div className="max-width">
            <div className="row text-center">
              <div className="col-md-7 m-t-40">
                <span className="text-muted display-5" />
                <div className="text-left ">
                  <h3 className="card-title">Front-End</h3>
                  <h6 className="card-subtitle">60%</h6>
                </div>
                <div className="progress">
                  <div
                    className="progress-bar bg-info"
                    role="progressbar"
                    style={{ width: '60%', height: 20 }}
                    aria-valuenow={25}
                    aria-valuemin={0}
                    aria-valuemax={100}
                  />
                </div>
                <span className="text-muted display-5" />
                <div className="text-left m-t-20">
                  <h3 className="card-title">Back-End</h3>
                  <h6 className="card-subtitle">65%</h6>
                </div>
                <div className="progress">
                  <div
                    className="progress-bar bg-info"
                    role="progressbar"
                    style={{ width: '65%', height: 20 }}
                    aria-valuenow={25}
                    aria-valuemin={0}
                    aria-valuemax={100}
                  />
                </div>
                <span className="text-muted display-5" />
                <div className="text-left m-t-20">
                  <h3 className="card-title">Programming</h3>
                  <h6 className="card-subtitle">70%</h6>
                </div>
                <div className="progress">
                  <div
                    className="progress-bar bg-info"
                    role="progressbar"
                    style={{ width: '70%', height: 20 }}
                    aria-valuenow={25}
                    aria-valuemin={0}
                    aria-valuemax={100}
                  />
                </div>
              </div>
              <div className="col-md-5 m-t-40">
                <div className="image-box">
                  <img
                    src={
                      process.env.PUBLIC_URL + '/images/proficiency_image.jpg'
                    }
                    alt="demo2"
                    className="img-responsive"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Proficiency;
