import React, { Component } from 'react';
import SocialFollow from './SocialFollow';

class Introduction extends Component {
  render() {
    return (
      <div className="light-blue">
        <div className="col-md-12" id="demos">
          <div className="max-width">
            <div className="row text-center">
              <div className="col-md-5 m-b-40">
                <div className="text-left ">
                  <h1 className="">HELLO, I'M</h1>
                  <h1 className="display-3 text-info">REY AQUINO</h1>
                  <h4>Web Developer | Web Application Developer</h4>
                  <p>Web Developer with almost 2 years of experience
                  in structuring, developing, implementing, hosting, testing, and debugging interactive websites and
                  web applications. Up to date with the latest tools
                  and technologies in both hardware and software
                  of computers.
                  </p>
                  <div className="m-t-40 social">
                    <SocialFollow />
                  </div>
                </div>

                <div className="down-btn m-t-40 text-left">
                  <a
                    href="https://drive.google.com/file/d/1MweDRHTrQP2sAAUEcZ2vfVI1tKOU83WK/view?usp=sharing"
                    className="btn btn-info btn-rounded m-b-10"
                  >
                    SEE MY RESUME
                  </a>
                </div>
              </div>
              <div className="col-md-7">
                <div className="image-box">
                  <img
                    src={
                      process.env.PUBLIC_URL +
                      '/images/Arctic-Code-Vault-Contributor.png'
                    }
                    alt="demo2"
                    className="img-responsive"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Introduction;
